.candy-shape {
    position: relative;
    z-index: 0;

    &__line {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        height: 1px;
        width: 100%;
        z-index: -1;
    }

    &__border {
        display: block;
        padding: 1px;
        width: 80%;
        height: 100%;
        margin: 0 auto;
        clip-path: $shape;
        text-align: left;
        outline: none;
        transition: all $transition;
    }

    &__content {
        height: 100%;
        clip-path: $shape;
        transition: all $transition;
    }
}